.LiveCountContainerParent {
    width: 100vw;
    height: 100vh;
    background: #fff;
}

.LiveCountContainer {
    width: 100%;
    height: 95%;
    display: flex;
    flex-wrap: nowrap;
    padding: 2rem 5rem;
    overflow-x: auto;
}

.locationContainer {
    width: auto;
    min-width: 425px;
    height: 100%;
    text-align: center;
    border: 0.2rem solid #4951ec;
    margin: 0rem 0.5rem;
}

.locationContainer h2 {
    background: #4951ec;
    color: #fff;
    font-weight: bold;
    text-transform: uppercase;
}

.locationUserContainer {
    padding: 1rem;
    overflow: auto;
    display: flex;
    flex-direction: column;
    flex-flow: row wrap;
    max-height: 90vh;
    justify-content: center;
}

.LiveCountContainerHeader{
    font-size: 1.15rem;
    background: #4951ec;
    font-weight: bold;
    color: #fff;
    padding: 0.25rem 1rem;
    height: 5%;
    align-items: center;
    display: flex;
}

/* Connect Card */

.connect-card {
    background: #fff;
    width: fit-content;
    border-radius: 0.2rem;
    margin: 1.00rem;
    box-shadow: 0px 0px 1rem rgb(73 81 236);
    transition: 0.25s;
}

.connect-card:hover {
    transform: scale(1.05);
    box-shadow: 0px 0px 0.25rem rgb(73 81 236);
    z-index: 5;
}

.connect-card-head {
    background: #E9E9E9;
    display: flex;
    align-items: center;
    padding: 1rem;
    border-top-left-radius: 0.2rem;
    border-top-right-radius: 0.2rem;
}

.connect-card-head-image {
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    border: 2px solid #00000081;
}

.connect-card-head-details {
    max-width: 14.5rem;
    margin: 0 0.8rem;
}

.connect-card-name {
    text-transform: uppercase;
    font-weight: bold;
    font-size: 1.25rem;
    color: black;
}

.connect-card-title {
    text-transform: uppercase;
    font-size: 1.0rem;
    font-weight: 500;
    color: #717171;
}

.connect-card-email {
    font-size: 1.15rem;
    font-weight: 500;
    color: #717171;
    margin-top: 0.8rem;
}

.connect-card-body {
    padding: 0.8rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
}

